


body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
        'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
        sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

body {
    background-color: #57adff;
}

table {
    /* font-size: 2vh; */
    font-family: "Lucida Sans Unicode", "Lucida Grande", Sans-Serif;
    border-spacing: 0;
    text-align: center;
    vertical-align: middle;

    width: 100%;
    white-space: nowrap;
    background-color: white;
}


button {
    height: 2em;
    border-radius: 7px;
}


#ContentWrapperAll {
    display:   inline-flex;
    width: 100%;
    overflow:  hidden
}

.buttonBar,
.bottomBar {
    display: inline-flex;
    width: 98vw;
    white-space: nowrap;
    margin: 0.5rem 0.5rem 0.5vw 0.5rem;;
}

.buttonBar button,
.bottomBar button {
    width: fit-content;
    min-width : 2rem;
    max-height: 2rem;

    border:none;
    box-shadow: 2px 2px 2px 1px #000000e3;
    font-size : 22px;
    background: white;
}

.bottomBar .navButton button{
    width: 3em;
    min-width: fit-content;
}

#reqDateButton {
    position  : relative;
    min-width : 7rem;

}

#reqDateInput,
#reqDateInput::-webkit-calendar-picker-indicator {
    position: absolute;
    top: 0;
    left: 0;
    margin-inline-start: 0;
    padding: 0;
    width: 95%;
    height: 95%;
    opacity: 0;
}

#reqDateInput::-webkit-datetime-edit { display: none;}

.inner {
    display: inline-flex;

    overflow-x: scroll;
    overflow-y: scroll;
    -webkit-overflow-scrolling: touch;

    width : 100%;
    height: 100%;

    margin: 0;

    border-radius: 5px;
}



.inner::-webkit-scrollbar {
    width : 0;
    height: 1em;
    background-color: rgba(36, 36, 255, 0.242);
    border-radius: 0 0 5px 5px;
}

.inner::-webkit-scrollbar-thumb {
    background-color:#b2e5ff;
    border-radius: 5px;
    box-shadow: inset 1px 1px 3px #4d94ff;
}
.inner::-webkit-scrollbar-thumb:hover {
    background-color:  #ffffff;
}

.inner::-webkit-scrollbar-button,
.inner::-webkit-scrollbar-corner {
    display: none;
}


#ContentWrapperInner{
    position: relative;
    display : inline-flex;

    margin  : max(0.5vw, 0.5em);
    overflow:  hidden;

    width : 97vw;
    height: 84vh;

    border       : none;
    border-radius: 5px;
    box-shadow   : 3px 3px 9px 3px #000000e3;
}


#testDiv {
    position: absolute;
    text-align: center;
    top: 5em;
    width: 100%;
    height: 1em;
    z-index: 100;
    font-size: 2em;
    font-weight: bold;
    background-color: red;
    color : white;

}